import { Component } from '@angular/core';
import { ActiveModalRef, RxSelectOption } from '@bmc-ux/adapt-angular';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'rx-filter-by-card-selection',
  templateUrl: './filter-by-card-selection-dialog.component.html'
})
export class RxFilterByCardSelectionDialogComponent {
  options: RxSelectOption[];
  field: RxSelectOption;

  alertConfig = {
    content: this.translateService.instant(
      'com.bmc.arsys.rx.client.view-components.record-grid.cards.filter-by-selection.dialog.info.message'
    ),
    type: 'section',
    dismissible: false,
    variant: 'info',
    icon: true
  };

  constructor(public context: ActiveModalRef, private translateService: TranslateService) {
    this.options = this.context.getData().filterableFields;
  }

  optionFormatter(option: RxSelectOption): string {
    return option.name;
  }

  apply(): void {
    this.context.close(this.field[0].id);
  }
}
